@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,600&display=swap);
body {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

::-moz-selection {
  background-color: #000000;
  color: #ffffff;
}

::selection {
  background-color: #000000;
  color: #ffffff;
}

main {
  position: relative;
  display: grid;
  width: 100%;
  overflow: hidden;
  justify-content: center;
  justify-items: center;
}

section {
  justify-self: center;
  position: relative;
  display: grid;
  align-content: start;
  max-width: 1050px;
  width: 100%;
  min-height: 100vh;
  padding: 10% 15px;
}

.section-name::first-letter {
  background-color: #000000;
  color: #ffffff;
  padding: 0px 4px;
}

.section-name {
  justify-self: start;
  position: relative;
  font-size: 40px;
  padding: 18px 0;
}

.section-name__background-text {
  position: absolute;
  left: 30px;
  -webkit-transform: translateY(-40%);
          transform: translateY(-40%);
  font-size: 180px;
  font-weight: 600;
  color: #eeeeee;
  z-index: -1;
}

.button {
  background-color: transparent;
  padding: 5px 10px;
  min-width: 70px;
  outline: none;
  cursor: pointer;
  border-radius: 2px;
}
.button--white {
  color: #ffffff;
  border: solid #ffffff 1px;
}
.button--black {
  color: #000000;
  border: solid #000000 1px;
}
.r-45 {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.r-45-m {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

a:link {
    text-decoration: inherit;
    color: inherit;
    cursor: auto;
}

a:visited {
    text-decoration: inherit;
    color: inherit;
    cursor: auto;
}

.curtain {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #141414;
    z-index: 5;
    -webkit-transition: height .6s ease-in-out;
    transition: height .6s ease-in-out;
}

/* appear */
.curtain[class~="appear"] {
    height: 0%;
}
.hero[class~="appear"] {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.menu__list__item[class~="appear"] {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
/* end appear */

.hero {
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr;
    justify-items: center;
    justify-content: center;
    align-items: center;

    min-width: 100vw;
    width: 100%;
    min-height: 100vh;
    -webkit-transition: -webkit-transform .6s ease-in-out;
    transition: -webkit-transform .6s ease-in-out;
    transition: transform .6s ease-in-out;
    transition: transform .6s ease-in-out, -webkit-transform .6s ease-in-out;
    -webkit-transform: scale(.85);
            transform: scale(.85);
}

.me {
    position: relative;
    display: grid;
}

.me__name {
    font-size: 40px;
    font-weight: 600;
}

.me__as {
    font-size: 16px;
    font-weight: 300;
}
.me__name, .me__as, .me__icon {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
    -webkit-transition: opacity .5s ease-in-out, -webkit-transform .5s ease-in-out;
    transition: opacity .5s ease-in-out, -webkit-transform .5s ease-in-out;
    transition: opacity .5s ease-in-out, transform .5s ease-in-out;
    transition: opacity .5s ease-in-out, transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}
.me__name[class~=appear], .me__as[class~=appear], .me__icon[class~=appear] {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}

.me__icon {
    width: 35px;
    height: 35px;
    cursor: pointer;
}

.me a {
    position: absolute;
    bottom: 0;
    left: 0;
    -webkit-transform: translateY(130%);
            transform: translateY(130%)
}

/* menu */
.menu {
    align-self: center;
    background-color: rgb(223, 223, 223);
    padding: 20px;
}

.menu__list {
    display: grid;
    grid-template-columns: 85px 85px;
    grid-gap: 10px;
}

.menu__list__item {
    position: relative;
    display: grid;
    justify-items: center;
    align-items: center;
    width: 85px;
    height: 85px;
    background-color: #141414;
    font-weight: 600;
    font-size: 14px;
    color: #ffffff;
    box-shadow: 2px 2px 10px -1px #5d5d5d;
    cursor: pointer;
    -webkit-transition: opacity .2s ease-in-out, -webkit-transform .2s ease-in-out;
    transition: opacity .2s ease-in-out, -webkit-transform .2s ease-in-out;
    transition: transform .2s ease-in-out, opacity .2s ease-in-out;
    transition: transform .2s ease-in-out, opacity .2s ease-in-out, -webkit-transform .2s ease-in-out;
    -webkit-transform: translate(15px, -15px);
            transform: translate(15px, -15px);
    opacity: 0;
}
.menu__list__item  a {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.menu__list__item:hover {
    -webkit-transform: translate(-2px, -2px);
            transform: translate(-2px, -2px)
}

.menu__list__item__body {
    display: grid;
    position: absolute;
    text-align: center;
}

.menu__list__item__body span:last-child {
    justify-self: center;
    align-self: center;
    position: absolute;
    font-size: 50px;
    opacity: .1;
    z-index: -1;
}

@media screen and (min-width: 768px) {
    .me__name {
        font-size: 60px;
    }

    .me__as {
        font-size: 24px;
    }

    .me__icon {
        width: 40px;
        height: 40px;
    }
    .menu__list {
        grid-template-columns: 110px 110px;
        grid-gap: 15px;
    }
    .menu__list__item { 
        width: 110px;
        height: 110px;
        font-size: 16px;
    }
    .menu__list__item__body span:last-child {
        font-size: 60px;
    }
}

@media screen and (min-width: 1024px) {
    .hero {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
    }
}
.about-me .description {
    max-width: 1024px;
}

.description p {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    -webkit-transition: opacity .6s ease-in-out, -webkit-transform .6s ease-in-out;
    transition: opacity .6s ease-in-out, -webkit-transform .6s ease-in-out;
    transition: opacity .6s ease-in-out, transform .6s ease-in-out;
    transition: opacity .6s ease-in-out, transform .6s ease-in-out, -webkit-transform .6s ease-in-out;
}
.description p[class~=active] {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
.skills__body {
    position: relative;
    display: grid;
    grid-gap: 25px;
    grid-template-columns: 300px;
    align-items: start;
}

.skills__body__icon {
    right: none;
    position: absolute;
    justify-self: center;
    align-self: center;
    z-index: -1;
    width: 100px;
    height: 100px;
}
.skills__body__icon img {
    position: absolute;
    width: 100px;
    height: 100px;
    opacity: .4;
    object-fit: contain;
}

.skills__container {
    display: grid;
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
    opacity: 0;
    -webkit-transition: opacity .3s ease-in-out, -webkit-transform .5s ease-in-out;
    transition: opacity .3s ease-in-out, -webkit-transform .5s ease-in-out;
    transition: opacity .3s ease-in-out, transform .5s ease-in-out;
    transition: opacity .3s ease-in-out, transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}
.skills__container[class~=active] {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}

.skills__container__name {
    font-weight: 600;
    letter-spacing: 2px;
}

.skills__container__list {
    display: grid;
    margin: 10px;
}

.skills__container__list__item {
    margin-right: 8px;
    cursor: default;
    justify-self: start;
}

@media screen and (min-width: 768px) {
    .skills__body {
        grid-template-columns: 300px 300px;
    }
    .skills__body__icon {
        right: 50px;
    }
}
.scale-enter {
    opacity: 0;
    -webkit-transform: scale(.8);
            transform: scale(.8);
}

.scale-enter-active {
    opacity: .4;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: opacity .1s ease-in, -webkit-transform .2s ease-in-out;
    transition: opacity .1s ease-in, -webkit-transform .2s ease-in-out;
    transition: opacity .1s ease-in, transform .2s ease-in-out;
    transition: opacity .1s ease-in, transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}

.scale-exit {
    opacity: .4;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.scale-exit-active {
    opacity: 0 !important;
    -webkit-transform: scale(.8);
            transform: scale(.8);
    -webkit-transition: opacity .1s, -webkit-transform .2s ease-in-out;
    transition: opacity .1s, -webkit-transform .2s ease-in-out;
    transition: opacity .1s, transform .2s ease-in-out;
    transition: opacity .1s, transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}
.projects__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 330px));
    justify-content: center;
    grid-gap: 8px;
}
.projects__list__item {
    position: relative;
    min-width: 300px;
    max-width: 330px;
    height: 200px;
    background-color: #000000;
    box-shadow: 0px 4px 12px -2px #868686;
    overflow: hidden;
    cursor: default;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
    opacity: 0;
    -webkit-transition: opacity .4s ease-in-out, -webkit-transform .4s ease-in-out;
    transition: opacity .4s ease-in-out, -webkit-transform .4s ease-in-out;
    transition: transform .4s ease-in-out, opacity .4s ease-in-out;
    transition: transform .4s ease-in-out, opacity .4s ease-in-out, -webkit-transform .4s ease-in-out;
}
.projects__list__item[class~=active] {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}

.projects__list__item img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
}

.project-body {
    position: absolute;
    display: grid;
    grid-template-rows: repeat(2, 25px) 1fr 1fr;
    align-content: start;
    align-items: start;
    grid-gap: 5px;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: rgba(0, 0, 0, 1);
    color: #ffffff;
    -webkit-transition: -webkit-transform .2s ease-in-out;
    transition: -webkit-transform .2s ease-in-out;
    transition: transform .2s ease-in-out;
    transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
    padding: 5px 15px 15px 15px;
    -webkit-transition: background-color .2s ease-in-out;
    transition: background-color .2s ease-in-out;
}
.projects__list__item:hover .project-body {
    background-color: rgba(0, 0, 0, .7);
}
.project-body__name {
    height: 30px;
    width: 100%;
    text-align: center;
}
.project-body__stack {
    font-weight: 600;
}
.project-body__stack span {
    margin-right: 5px;
}
.project-body__description {
    font-weight: 300;
}
.project-body__buttons {
    align-self: end;
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 1fr;
}
.project-body__date {
    position: absolute;
    font-size: 8px;
    padding: 10px;
    top: 0;
    right: 0;
}
#contact a {
    cursor: pointer;
}

#contact p {
    margin: 6px 0;
    opacity: 0;
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
    -webkit-transition: opacity .6s ease-in-out, -webkit-transform .5s ease-in-out;
    transition: opacity .6s ease-in-out, -webkit-transform .5s ease-in-out;
    transition: opacity .6s ease-in-out, transform .5s ease-in-out;
    transition: opacity .6s ease-in-out, transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}
#contact p[class~=active] {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
nav {
    margin-top: 15px;
    margin-left: -15px;
    position: fixed;
    max-width: 1050px;
    width: 100%;
    z-index: 15;
    overflow: hidden;
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    -webkit-transition: opacity ease-in-out .5s .5s, -webkit-transform ease-in-out .5s .5s;
    transition: opacity ease-in-out .5s .5s, -webkit-transform ease-in-out .5s .5s;
    transition: transform ease-in-out .5s .5s, opacity ease-in-out .5s .5s;
    transition: transform ease-in-out .5s .5s, opacity ease-in-out .5s .5s, -webkit-transform ease-in-out .5s .5s;
}

nav[class~=active] {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

.nav__container {
    position: relative;
    display: grid;
}
.nav__body {
    justify-self: end;
    display: none;
    align-content: start;
    padding: 15px;
    grid-gap: 20px;
    background-color: #000000;
    border: solid #ffffff 1px;
    border-radius: 2px;
    right: 0;
    margin-top: -1px;
    z-index: 4;
    color: #ffffff;
    overflow: hidden;
    -webkit-transform: translateX(105%);
            transform: translateX(105%);
    opacity: 0;
}
.nav__body[class~=active] {
    display: grid;
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%)
}
.nav__body a {
    opacity: 0;
    cursor: pointer;
}
.nav__body a[class~=active] {
    -webkit-transition: opacity .4s ease-in-out;
    transition: opacity .4s ease-in-out;
    opacity: 1;
}

.nav__button {
    justify-self: end;
    display: grid;
    align-content: center;
    grid-gap: 4px;
    padding: 5px;
    width: 34px;
    height: 34px;
    background-color: #000000;
    border: solid 1px #ffffff;
    border-radius: 2px;
    z-index: 5;
}
.nav__button__line {
    width: 100%;
    height: 1px;
    background-color: #ffffff;
}
.fade-enter {
    opacity: 0;
    -webkit-transform: translateY(25%);
            transform: translateY(25%);
  }
  .fade-enter-active {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    -webkit-transition: opacity .5s ease-in, -webkit-transform .5s ease-in-out;
    transition: opacity .5s ease-in, -webkit-transform .5s ease-in-out;
    transition: opacity .5s ease-in, transform .5s ease-in-out;
    transition: opacity .5s ease-in, transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
  }
  .fade-exit {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  .fade-exit-active {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    -webkit-transition: opacity .5s, -webkit-transform .5s ease-in-out;
    transition: opacity .5s, -webkit-transform .5s ease-in-out;
    transition: opacity .5s, transform .5s ease-in-out;
    transition: opacity .5s, transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
  }
